var Web3 = require('web3');
var contract = require("truffle-contract");



import CryptoPostCards_Artifacts from '../build/contracts/CryptoPostCards.json'

var CryptoPostCards = contract(CryptoPostCards_Artifacts);

function loadArt(artId, artName, hashNumber, artPrice, artNextPrice, ownerAddress, locallyOwned) {
    
    var cardTemplate = $('#card-template');

    if (locallyOwned) {
        var cardRow = $('#card-row2');
        var titulo = $('#card-container-own');
        titulo.find('.oculto').css('display', 'initial');
        cardTemplate.find('.btn-buy').attr('disabled', true);
    } else {
        var cardRow = $('#card-row');
        cardTemplate.find('.btn-buy').removeAttr('disabled');

    }


    cardTemplate.find('.art-name').text(artName);
    cardTemplate.find('.id-owner').text(artId);
    cardTemplate.find('.art-name').attr("href", artId + '.html');
    cardTemplate.find('.art-img').attr('src', '/img/' + artId + '.png');
    cardTemplate.find('.art-img').attr('src', 'https://ipfs.infura.io/ipfs/' + hashNumber);
    cardTemplate.find('.art-owner').text(ownerAddress);
    cardTemplate.find('.art-owner').attr("href", "https://ropsten.etherscan.io/address/" + ownerAddress);
    cardTemplate.find('.btn-buy').attr('data-id', artId);
    cardTemplate.find('.art-price').text(parseFloat(artPrice).toFixed(3) + " ");
    cardTemplate.find('.art-next-price').text(parseFloat(artNextPrice).toFixed(3));

    cardRow.append(cardTemplate.html());
}



var App = {
    contracts: {},
    CryptoPostCardsAddress: '0x615be4a028f947aa61469681ef7c987c4e6974c4', // PUT YOUR CONTRACT HERE, NOW ROPSTEN


    init() {
        return App.initWeb3();
        if (!web3.isConnected()) {

            // show some dialog to ask the user to start a node

        } else {

            // start web3 filters, calls, etc

        }
    },


    initWeb3() {

        if (typeof web3 !== 'undefined') {
            // Use Mist/MetaMask's provider
            window.web3 = new Web3(web3.currentProvider);
          
        } else {
            console.warn("No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live, as it's inherently insecure. Consider switching to Metamask for development. More info here: http://truffleframework.com/tutorials/truffle-and-metamask");
            // fallback - use your fallback strategy (local node / hosted node + in-dapp id mgmt / fail)
            window.web3 = new Web3(new Web3.providers.HttpProvider("http://127.0.0.1:8545"));
            $("#msg").show();
            $("#msg").html(" Necesitas una billetera Web3 Ethereum para coleccionar Crypto PostCards.\n\n Recomendamos  <a href='https://metamask.io/'>Metamask</a> para desktop o Trust para mobil.\n\n Puedes leer las FAQS para ms informacin.");
            console.error(" Necesitas una billetera Web3 Ethereum para coleccionar Crypto PostCards.\n\n Recomendamos  <a href='https://metamask.io/'>Metamask</a> para desktop o Trust para mobil.\n\n Puedes leer las FAQS para ms informacin.");
        }
        
        web3.eth.getAccounts(function(error, accounts) {
            var address = accounts[0];
            console.log("Usando wallet: " + address)
            

            
          })
        return App.initContract();
    },

    initContract() {

        App.contracts.CryptoPostCards = CryptoPostCards;
        App.contracts.CryptoPostCards.setProvider(web3.currentProvider);

        App.loadartworks();
        App.getCharity();
        App.bindEvents();
    },

    loadartworks() {

        web3.eth.getAccounts(function(err, accounts) {
          if (err != null) {
            console.error("Error: " + err);
          } else if (accounts.length == 0) {
            $("#msg").show();
            $("#msg").html(" El usuario no ha iniciado sesin en MetaMask");
            console.log("El usuario no ha iniciado sesin en MetaMask");
          } else {
            $('#card-row').children().remove();
          }
        });

        // Get local address so we don't display our owned ite
        var totalSupply;

        web3.eth.getAccounts(function(error, accounts) {
            var address = accounts[0];
            //var addressCharity = accounts[9]

            var menuTemplate = $('#navbarText');

            if (accounts[0] == "0x2105e149d6cdebc39979cb8a06eac4f9c324d8a1") {

                menuTemplate.find('.oculto').css('display', 'initial');
            } else {
                menuTemplate.find('.oculto').css('display', 'none');
            }

        let contractInstance = App.contracts.CryptoPostCards.at(App.CryptoPostCardsAddress);
        return totalSupply = contractInstance.totalSupply().then((supply) => {
            for (var i = 0; i < supply; i++) {
                console.log(i);
                App.getArtDetails(i, address);
            }
        }).catch((err) => {
            console.log(err.message);
        });
    });
    },

    getArtDetails(artId, localAddress) {
        console.log("artID: " + artId + "-----" + "localAddress: " + localAddress);
        let contractInstance = App.contracts.CryptoPostCards.at(App.CryptoPostCardsAddress);
        return contractInstance.getToken(artId).then((art) => {
            var artJson = {
                'artId': artId,
                'artName': art[0],
                'hashNumber': art[1],
                'artPrice': web3.fromWei(art[2]), 
                'artNextPrice': web3.fromWei(art[3]),
                'ownerAddress': art[4]
            };
            console.log(artJson);
            console.log("artID: " + artId + "-----" + "artName: " + art[0] + "-----" + "HashNumber: " + art[1] + "-----" + "artPrice: " + art[2] + "-----" + "artNextPrice " + art[3] + "-----" + "ownerAddress: " + art[4]);
            // Compruebe si somos dueos de alguna cryptopostcard
            if (artJson.ownerAddress !== localAddress) {
                loadArt(
                    artJson.artId,
                    artJson.artName,
                    artJson.hashNumber,
                    artJson.artPrice,
                    artJson.artNextPrice,
                    artJson.ownerAddress,
                    false
                );
            } else {
                loadArt(
                    artJson.artId,
                    artJson.artName,
                    artJson.hashNumber,
                    artJson.artPrice,
                    artJson.artNextPrice,
                    artJson.ownerAddress,
                    true
                );
            }
        }).catch((err) => {
            console.log(err.message);
        })
    },

    getCharity(){

            var addresscharity;
            var wei;
            var balance;
            addresscharity = '0x7d17b0399552c376ee8e4888df05390f86bfbe6d'
            try {
                web3.eth.getBalance(addresscharity, function (error, wei) {
                    if (!error) {
                        //var balance = web3.fromWei(wei, 'ether');
                        var balance = web3.fromWei(wei);
                        var charityamount = $('#charity-amount');
                        charityamount.find('.charity-amount').text(balance);
                    }
                });
            } catch (err) {

                console.log(err);
            }

    },




    /** Event Bindings for Form submits */
    bindEvents() {

     $(document).on('submit', 'form.art-purchase', window.uclicked(this));

    },
};

jQuery(document).ready(
    function($) {

        App.init();
    }
);



window.uclicked = function (identifier) { 
 
    event.preventDefault();

    var artId = parseInt($(identifier).data('id'));
    console.log ("ID: " + artId);
    web3.eth.getAccounts((error, accounts) => {
        if (error) {
            console.log(error);
        }
        var account = accounts[0];

        let contractInstance = App.contracts.CryptoPostCards.at(App.CryptoPostCardsAddress);
        contractInstance.priceOf(artId).then((price) => {
           
            return contractInstance.purchase(artId, {
                from: account,
                value: price,
            }).then(result => App.loadartworks()).catch((err) => {
            
                console.log(err.message);
                if(err == null){
                    $("#msg").show();
                    $("#msg").html("Ha comprado la Crypto PostCard con xito!  ");
                }else{
                
                }
            });
        });
    });
  


  
}


